<template>

<div>

    <div class = "banner" :style ="banner">
        <div class="banner_title">
        <span class="title"></span>
        </div>
    </div>
    
    <div class="new_list">
        <div class = "date_show">
            <span>{{new_show.date}}</span>
        </div>
        <div class = "new_title">
            <h1>{{new_show.title}}</h1>
        </div>
    </div>

    <div  v-html="new_show.text" class="new_content" >
     
    </div>

    <div class="some_news">
        <div>
            <h1></h1>
        </div>

        <a href="#">
            2020
        </a>
    </div>
    

</div>
</template>

<script>



import Bread from '@/components/BreadCrumb'
export default {
  name: 'LongyiNew',
  data(){
    return {
		banner: {
            backgroundImage:"url(" + require("./../assets/banner.jpg") + ")"
        },
        new_show:{
            date : '2021-01-04',
            title : '  ',
            text : this.getext()
        },
        news: {
            image:require("./../assets/new_header.jpg") 
        }
    }
  },
  components: {
        
        Bread
  },
  methods:{
      goNew(){
          alert('test');
      },
      getext(){
          var text =  "2020()";
           text = text +"</br><img src='/longyi/static/img/new_header.98d2ac8.jpg'></br>";
           text = text +"21202015";
         return text;
      }
  },
  computed:{

  }
}
</script>

<style>

.banner_title .title{
    font-family: ', "Microsoft JhengHei", Arial, Helvetica;';
}


.new_list{
    border-top: solid 2px #EBEBEB;
    border-block: solid 2px #EBEBEB;
}
.date_show{
    margin: 20px 40px 10px 20px;
    padding: 5px 10px 5px 10px;
    width:160px;
    background: lightblue;
}

.date_show span{
    padding-left: 20px;
    font-size: large;
    color:gray;
}

.new_title{
    margin-left: 20px;
}

.new_content{
    border-block: solid 2px #EBEBEB;
    padding:50px;
    font-size :x-large;
}

.some_news{
    padding: 10px 50px;
    border-block: solid 2px #EBEBEB;
    font-size :x-large;
    color:gray;
}
</style>

